.block.block-cta-with-image {
  text-align: center;

  &.block-latest-blog-post {
    text-align: left;

    .button-wrapper {
      margin-bottom: 0;
    }

    a {
      color: $color-midnight-blue;
    }

    .col.col-image {
      height: 100%;
    }

    .blog-post-time {
      font-size: 13px;
    }
  }

  .container {
    overflow: visible;
  }

  .cols {
    @include grid(2, $gutter_x: 0rem, $gutter_y: 0rem);
  }

  h2 {
    @extend %general-heading-cta;
    font-weight: 400;
    margin-bottom: $margin-between-text-elements;
  }

  h2,
  p {
    color: $color-midnight-blue;
  }

  .text-content {
    margin-bottom: $margin-between-text-elements;

    p:last-child {
      margin-bottom: 0;
    }
  }

  .cta-link {
    color: $color-midnight;

    &::after {
      background-color: $color-midnight;
    }
  }

  .cta-link-wrapper {
    margin-bottom: 0;
  }

  .col.col-content {
    background-color: $color-mint;
    color: $color-midnight;
    padding-top: $padding-blog-content-box-y;
    padding-bottom: $padding-blog-content-box-y;
    padding-left: $padding-blog-content-box-x;
    padding-right: $padding-blog-content-box-x;
    transform: translate($offset-x-minus/2, $offset-y);
    position: relative;
    z-index: 2;
    margin-right: $offset-x-minus / 2;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
  }

  .col.col-image {
    height: 46rem;
    overflow: hidden;
    position: relative;
    transform: translateX($offset-x/2);
    margin-left: $offset-x-minus / 2;
  }
}

.block.block-values + .block.block.block-cta-with-image {
  padding-top: 0;
}

.block.block-hero + .block-with-image {
  padding-top: $padding-blocks-medium;
}
